    width: 24px;
    height: 21px;
    border: 3px solid #5fadbf;
    border-radius: 5px;
    position: relative;
    cursor: pointer;
    transition: all 0.1s ease-in-out;
  }
  .lock:after {
    content: "";
    display: block;
    background: #5fadbf;
    width: 3px;
    height: 7px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -3.5px 0 0 -2px;
    transition: all 0.1s ease-in-out;
  }
  .lock:before {
    content: "";
    display: block;
    width: 15px;
    height: 15px;
    bottom: 100%;
    position: absolute;
    left: 50%;
    margin-left: -8px;
    border: 3px solid #5fadbf;
    border-top-right-radius: 50%;
    border-top-left-radius: 50%;
    border-bottom: 0;
    transition: all 0.1s ease-in-out;
  }
  /* Locked Hover */
  .lock:hover:before {
  }
  /* Unlocked */
  .unlocked {
  }
  .unlocked:before {
    bottom: 130%;
    left: 10%;
    transform: rotate(-45deg);
  }
  .unlocked,
  .unlocked:before {
    border-color: #ff5153;
  }
  .unlocked:after {
    background: #ff5153;
  }
  /* Unlocked Hover */
}

